import {post} from "./api";
const uri = 'http://result.eolinker.com/8RiESwDd5a5506d56f9453f947d0ff45a1b5e487a9b6182?uri='
export const setting = {
  getList(params){
    return post(
      {baseUri: uri, url: '/setting/keywords/getList'},
      params
    )
  },
  getList1(params){
    return post(
      {baseUri: uri, url: '/setting/rule/getList'},
      params
    )
  }
}



